import React, { useCallback, useEffect, useState } from 'react';
import { navigate } from 'gatsby';
import Page from 'components/Layout/Page';
import { Container, Grid, Typography, Paper, makeStyles, Snackbar } from '@material-ui/core';
import MentoringApplyForm from 'components/Forms/MentoringApply';
import { MentoringApplyFormDto } from 'src/models/forms';
import { applyMentoringForm } from 'remotes/lubycon';
import { MENTORING_GROUP, MENTORING_EXPIRY } from 'src/constants';
import * as Sentry from '@sentry/react';
import Countdown from 'components/Utils/CountDown';
import { useCountDown } from 'hooks/useCountDown';
import { generateLogger } from 'src/utils/logger';

const useStyles = makeStyles(({ spacing, typography, breakpoints }) => ({
  header: {
    marginTop: spacing(15),
    marginBottom: spacing(4),
  },
  title: {
    fontWeight: typography.fontWeightBold,
    textAlign: 'center',
  },
  timer: {
    textAlign: 'center',
  },
}));

const loggger = generateLogger('mentoring_project_apply_page');

const MentoringApply = () => {
  const styles = useStyles();
  const [openErrorSnackbar, setOpenErrorSnackbar] = useState(false);
  const [loading, setLoading] = useState(false);
  const { expired } = useCountDown(MENTORING_EXPIRY);
  const handleSubmit = useCallback(
    async (data: MentoringApplyFormDto) => {
      if (expired) {
        return;
      }

      setLoading(true);
      loggger.click('mentoring_project_form_apply', {
        group: MENTORING_GROUP,
        position: data.position,
        careerYear: data.careerYear,
      });
      try {
        await applyMentoringForm(data);
        navigate('/mentoring/apply-result');
      } catch (e) {
        setOpenErrorSnackbar(true);
        Sentry.captureException(e, {
          user: {
            email: data.email,
            username: data.name,
          },
        });
      } finally {
        setLoading(false);
      }
    },
    [expired]
  );

  const title = '루비콘 멘토링 프로젝트 신청하기';
  const description = `루비콘 멘토링 프로젝트 ${MENTORING_GROUP}기에 오신 것을 환영합니다!`;

  const handleSnackbarClose = useCallback(() => setOpenErrorSnackbar(false), []);

  useEffect(() => {
    loggger.view();
  }, []);

  return (
    <Page pageName={title} showTitle={false} description={<Typography component="span">{description}</Typography>}>
      <Container>
        <Grid container justify="center" className={styles.header}>
          <Grid item xs={12} md={8}>
            <Typography variant="h4" component="h1" gutterBottom={true} className={styles.title}>
              {MENTORING_GROUP}기 {title}
            </Typography>
            <div className={styles.timer}>
              <Countdown expiry={MENTORING_EXPIRY} expiredText="" />
            </div>
          </Grid>
        </Grid>
        <Grid container justify="center">
          <Grid item xs={12} md={8}>
            <MentoringApplyForm loading={loading} onSubmit={handleSubmit} />
          </Grid>
        </Grid>
      </Container>
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        open={openErrorSnackbar}
        autoHideDuration={5000}
        onClose={handleSnackbarClose}
        message={`멘토링 프로젝트 신청에 약간의 문제가 있어요!😢 문제가 계속 된다면 lubycon@gmail.com으로 알려주세요`}
      />
    </Page>
  );
};

export default MentoringApply;
