import React, { useCallback, useMemo } from 'react';
import {
  Grid,
  FormControlLabel,
  Checkbox,
  TextField,
  Radio,
  Button,
  RadioGroup,
  makeStyles,
  colors,
  Typography,
  Paper,
} from '@material-ui/core';
import { useForm } from 'react-hook-form';
import Section from 'components/Forms/MentoringApply/Section';
import Fieldset from 'components/Forms/MentoringApply/Fieldset';
import { MentoringApplyFormDto } from 'models/forms';
import { positions } from 'src/constants/applyForm';
import Loading from 'components/Utils/Loading';
import { MENTORING_EXPIRY, MENTORING_GROUP } from 'src/constants';
import { useCountDown } from 'src/hooks/useCountDown';
import Emoji from 'src/components/Utils/Emoji';

const useStyles = makeStyles(({ palette, spacing, shape, typography, breakpoints }) => ({
  root: {
    '& .MuiFormLabel-asterisk': {
      fontWeight: 'bold',
      color: palette.primary.main,
    },
  },
  options: {
    borderRadius: shape.borderRadius,
    marginRight: 7,
    paddingRight: 9,
    '&:hover': {
      backgroundColor: palette.action.hover,
    },
  },
  expiredText: {
    textAlign: 'center',
    marginBottom: spacing(3),
  },
  description: {
    margin: '-1rem 0px 2rem',
    color: 'rgba(0, 0, 0, 0.54)',
    fontStyle: 'italic',
  },
  mailLink: {
    color: palette.primary.main,
    fontWeight: typography.fontWeightBold,
  },
  paper: {
    padding: spacing(2),
    [breakpoints.up('md')]: {
      padding: spacing(4),
    },
    '&+&': {
      marginTop: spacing(4),
    },
  },
  cta: {
    fontSize: typography.h6.fontSize,
    fontWeight: typography.fontWeightBold,
  },
}));

interface Props {
  onSubmit?: (data: MentoringApplyFormDto) => void;
  loading?: boolean;
}
const MentoringApplyForm = ({ onSubmit, loading = false }: Props) => {
  const styles = useStyles();
  const { register, handleSubmit, errors, watch } = useForm();
  const formValues = watch();
  const { expired } = useCountDown(MENTORING_EXPIRY);

  const submitButtonText = useMemo(() => (expired ? '마감되었습니다' : '멘토링 참여하기'), [expired]);

  const onSubmitHandler = useCallback(
    data => {
      const formData = { ...data } as MentoringApplyFormDto;
      onSubmit?.(formData);
    },
    [onSubmit]
  );

  const questionsByPosition = useMemo(() => {
    switch (formValues.position) {
      case '웹 프론트엔드 개발자':
        return 프론트질문리스트;
      case '백엔드 개발자':
        return 백엔드질문리스트;
      case 'UI/UX 디자이너':
        return 디자인질문리스트;
      default:
        return [];
    }
  }, [formValues.position]);

  if (expired) {
    return (
      <>
        <Typography className={styles.expiredText} gutterBottom>
          {`멘토링 프로젝트 ${MENTORING_GROUP}기 신청이 마감되었습니다.`}
          <br />
          다음 기회에 다시 신청해주세요! <Emoji emoji="🙇‍♂️" name="thanks" />
        </Typography>
        <Button href="/" color="primary" variant="contained" fullWidth>
          홈으로
        </Button>
      </>
    );
  }

  return (
    <Grid
      component="form"
      container
      noValidate
      autoComplete="off"
      onSubmit={(e: any) => e.preventDefault()}
      className={styles.root}
    >
      <Paper className={styles.paper}>
        <Section title="연락에 필요한 기본정보를 작성해주세요.">
          <Fieldset label="이메일" required={true} error={Boolean(errors.email)}>
            <TextField
              name="email"
              type="email"
              placeholder="lubycon@gmail.com"
              variant="outlined"
              inputRef={register({
                required: {
                  value: true,
                  message: '이메일을 입력해주세요',
                },
                pattern: {
                  value: /\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/,
                  message: '올바른 이메일을 입력해주세요',
                },
              })}
              error={Boolean(errors.email)}
              helperText={errors.email?.message ?? ''}
            />
          </Fieldset>
          <Fieldset label="이름" required={true} error={Boolean(errors.name)}>
            <TextField
              name="name"
              placeholder="홍길동"
              variant="outlined"
              inputRef={register({
                required: {
                  value: true,
                  message: '이름을 입력해주세요',
                },
                pattern: {
                  value: /[a-zA-Z가-힣]+/,
                  message: '올바른 이름을 입력해주세요',
                },
              })}
              error={Boolean(errors.name)}
              helperText={errors.name?.message ?? ''}
            />
          </Fieldset>
          <Fieldset label="휴대폰 번호 (-는 빼주세요)" required={true} error={Boolean(errors.phone)}>
            <TextField
              name="phone"
              type="tel"
              placeholder="01012341234"
              variant="outlined"
              inputRef={register({
                required: {
                  value: true,
                  message: '휴대폰 번호를 입력해주세요',
                },
                pattern: {
                  value: /^01\d\d{4}\d{4}$/,
                  message: '올바른 휴대폰 번호를 입력해주세요',
                },
              })}
              error={Boolean(errors.phone)}
              helperText={errors.phone?.message}
            />
          </Fieldset>
        </Section>
        <Section title="현재 무슨 일을 하고 계시나요?">
          <Fieldset label="포지션" required={true}>
            <RadioGroup row defaultValue={positions[0]}>
              {positions.map(position => (
                <FormControlLabel
                  key={position}
                  name="position"
                  label={position}
                  value={position}
                  control={<Radio />}
                  inputRef={register()}
                  className={styles.options}
                />
              ))}
            </RadioGroup>
          </Fieldset>
          <Fieldset label="이 포지션의 경력이 어떻게 되시나요? (연차)" required={true}>
            <TextField name="careerYear" type="number" variant="outlined" inputRef={register()} />
          </Fieldset>
        </Section>
        <Section title="여러분을 알려주세요.">
          {필수질문리스트.map(question => (
            <Fieldset key={question.key} label={question.label} required={question.required}>
              <TextField multiline name={question.key} variant="outlined" inputRef={register()} />
            </Fieldset>
          ))}
        </Section>
        <Section title={`${formValues.position}에 대한 질문이에요.`}>
          {questionsByPosition.map(question => (
            <Fieldset key={question.key} label={question.label} required={question.required}>
              <TextField multiline name={question.key} variant="outlined" inputRef={register()} />
            </Fieldset>
          ))}
        </Section>
      </Paper>

      <Paper className={styles.paper}>
        <Section title="이제부터는 선택 항목이에요.">
          <p className={styles.description}>
            처음 만나는 사람들과 팀을 이루고 짧은 시간 동안 하나의 서비스를 완성하는 것은 쉽지 않은 일이니만큼, 멘토들이
            여러분에 대해 더 많이 알 수록 시너지를 낼 수 있는 더 좋은 팀을 꾸릴 수 있을 거예요.
          </p>
          {선택질문리스트.map(question => (
            <Fieldset key={question.key} label={question.label} required={question.required}>
              <TextField multiline name={question.key} variant="outlined" inputRef={register()} />
            </Fieldset>
          ))}
          <Typography component="h3" variant="h6" style={{ margin: '0 0 1rem' }} gutterBottom={true}>
            포트폴리오/깃헙/블로그 링크 (선택)
          </Typography>
          <p className={styles.description}>
            파일인 경우에는{' '}
            <a className={styles.mailLink} href="mailto:lubycon@gmail.com" target="_blank" rel="noreferrer">
              lubycon@gmail.com
            </a>
            으로 보내주세요!
          </p>
          <Fieldset label={'링크 1'} required={false}>
            <TextField type="url" name="link1" variant="outlined" inputRef={register()} />
          </Fieldset>
          <Fieldset label={'링크 2'} required={false}>
            <TextField type="url" name="link2" variant="outlined" inputRef={register()} />
          </Fieldset>
          <Fieldset label={'링크 3'} required={false}>
            <TextField type="url" name="link3" variant="outlined" inputRef={register()} />
          </Fieldset>
        </Section>
      </Paper>

      <Paper className={styles.paper}>
        <Section>
          <Fieldset
            label="인재풀 확보에 동의해주신다면, 만약 멘토링 프로젝트에서 떨어지더라도 프로젝트 중간에 인원 충원이 필요할
            경우에 가장 먼저 연락을 드릴 수 있습니다!"
          >
            <FormControlLabel
              name="agreeUsePrivacyData"
              label="동의합니다"
              inputRef={register()}
              control={<Checkbox />}
            />
          </Fieldset>
          <Button
            color="primary"
            size="large"
            variant="contained"
            fullWidth={true}
            onClick={handleSubmit(onSubmitHandler)}
            disabled={loading || expired}
            className={styles.cta}
          >
            {loading ? <Loading size={30} color={colors.grey['800']} /> : submitButtonText}
          </Button>
        </Section>
      </Paper>
    </Grid>
  );
};

export default MentoringApplyForm;

const 필수질문리스트: 질문[] = [
  {
    key: 'wantGet',
    label: '이 멘토링을 통해 얻어가고 싶은 게 무엇인가요?',
    required: true,
  },
  {
    key: 'happyMoment',
    label: '개발자/디자이너를 하면서 가장 즐거웠던 기억은 무엇이었나요?',
    required: true,
  },
  {
    key: 'concernPoint',
    label: '프로젝트를 참여하기 전에 걱정되는 부분이 있나요?',
    required: true,
  },
];
const 선택질문리스트: 질문[] = [
  {
    key: 'tryDo',
    label: '평소에 실력을 키우기 위해 어떤 노력을 하고 계신가요?',
    required: false,
  },
  {
    key: 'painPointByCoWorker',
    label: '팀원들과의 원활하지 않은 커뮤니케이션 떄문에 힘들었던 적이 있으셨나요? 그 문제를 어떻게 해결하셨었나요?',
    required: false,
  },
  {
    key: 'importantThingForComunication',
    label: '협업에서 가장 신경써야 할 점이 무엇이라고 생각하시나요? 이유도 적어주세요.',
    required: false,
  },
  {
    key: 'wantTeam',
    label: '이 프로젝트에서 어떤 팀을 만나기를 기대하나요?',
    required: false,
  },
];

const 프론트질문리스트: 질문[] = [
  { key: 'fe1', label: '최근 겪었던 기술적인 문제 중 가장 어려웠던 것이 무엇이었나요?', required: true },
  { key: 'fe2', label: '최근 가장 흥미가 가는 기술은 무엇인가요?', required: true },
  {
    key: 'fe3',
    label: '지금까지 했던 프로젝트 중 가장 인상깊었던 프로젝트가 무엇인가요?',
    required: true,
  },
];
const 백엔드질문리스트: 질문[] = [
  { key: 'be1', label: '경험해본 웹 어플리케이션 배포 프로세스를 설명해주세요.', required: true },
  { key: 'be2', label: '능숙하게 사용할 줄 아는 개발 언어와 프레임워크를 나열해주세요.', required: true },
  {
    key: 'be3',
    label: '해결하기 가장 어려웠던 걸린 이슈가 무엇인가요? 그리고 어떤 방식으로 해결했는지 알려주세요.',
    required: true,
  },
];
const 디자인질문리스트: 질문[] = [
  {
    key: 'design1',
    label: '디자이너로써 본인의 강점은 무엇이라고 생각하나요?',
    required: true,
  },
  {
    key: 'design2',
    label: '새로운 서비스를 기획하고 시각화해본 경험이 있나요? 그 과정에서 어떤 문제를 해결했나요?',
    required: false,
  },
];

interface 질문 {
  key: string;
  label: string;
  required: boolean;
}
